/*global chrome*/

import React, { useState, useEffect } from "react";
import { Grid, TextField, Button, MenuItem } from "@mui/material";
import LogoutIcon from '@mui/icons-material/Logout';
import { Container } from "react-bootstrap";
import { AuthProvider, useAuth } from "../../contexts/AuthContext";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import AcUnitIcon from '@mui/icons-material/AcUnit';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import { IconButton } from "@mui/material";

import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';

import JotFind from "./main"
import Settings from "./setting"
import SignIn from "./login";
import X from './x.css'
import {ReactComponent as Knoxlogo} from '../icons/knoxlogo.svg'
import { WidgetsOutlined } from "@mui/icons-material";




if (browser == undefined || browser == null) {
	var browser = chrome;
}



const Frame = () => {

	const [theme, setTheme] = useState(localStorage.getItem('theme') === 'dark' ? 'dark' : 'light');
	useEffect(() => {
		localStorage.setItem('theme', theme);
	}, [theme]);
	const handleTheme = () => {
		setTheme(theme === 'dark' ? 'light' : 'dark');
	};
	const colorTheme = createTheme({
		palette: {
			mode: theme,
		},
	});

	const [settings_page, set_settings_page] = useState(false);

	const user = useAuth()?.currentUser;
	const details = useAuth()?.value;

	const logout = useAuth()?.logout;
	const isSignInLink = useAuth().isSignInLink;
	const signInLink = useAuth().signInLink;

	const [email, setEmail] = useState("")
	const [link, setLink] = useState("")

	const [wid, set_wid] = useState(400);
	

	chrome.storage.local.get(['width'], function (e) {
		set_wid(e.width);
	});

	browser.storage.local.get(['emailForSignIn'], function (e) {
		if (email != e.emailForSignIn) {
			setEmail(e.emailForSignIn);
		}
	});
	browser.storage.local.get(['linkForSignIn'], function (e) {
		if (email != e.linkForSignIn) {
			setLink(e.linkForSignIn);
		}
	});

	if (!user && email && link && isSignInLink(link)) {
		signInLink(email, link)
			.then(() => {
				chrome.storage.local.remove(['emailForSignIn', 'linkForSignIn'])
			})
			.catch((error) => {
				alert(error);
			});
	}

	

	return (

		<ThemeProvider theme={colorTheme} >
			<CssBaseline />

			<div sx={{ width: wid }}>

				<Grid container spacing={2}>

					<Grid align="left" item xs={1}>
						<Tooltip title="Settings">
						<IconButton
						    className="icon-button-blue"
							disableRipple
							onClick={() => {
								set_settings_page(settings_page == false);
							}}>
							<Knoxlogo/>
						</IconButton>
						</Tooltip>
					</Grid>



					<Grid item xs={9}/>

					<Grid item xs={2} align="right">
						<Tooltip title="Change Theme">
						<IconButton onClick={handleTheme} color="inherit" sx={{ opacity: 0.1 }} align="right" className="icon-button" disableRipple>
							{theme === 'dark' ? <Brightness4Icon /> : <Brightness7Icon />}
						</IconButton>
						</Tooltip>
					</Grid>

				</Grid>


				{ user ? (
					<>
					    

						{/* <Button
							className="d-flex align-items-center justify-content-center"
							onClick={logout}>
							<LogoutIcon />
						</Button> */}

						{settings_page == false ? (
							<JotFind sx={{ width: wid }} />
						) : (
							<Settings sx={{ width: wid }} />
						)}

					</>
				) : (


					<Container
						className="d-flex align-items-center justify-content-center"
						sx={{ width: wid }}>
						{/* {{user} ? ("Hello" - {user}) : "undefined"} */}
							<div className="w-100" sx={{ width: wid }}>
								<SignIn sx={{ width: wid }}/>
							</div>
				</Container>

				)}
			</div>
		</ThemeProvider>
	)
}

export default Frame;
