/*global chrome*/

import React, { useState, useEffect } from "react";
import { Grid, TextField, Button, MenuItem, Typography } from "@mui/material";
import { useNavigate } from "react-router-dom"
import axios from "axios"
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import ClearIcon from '@mui/icons-material/Clear';
import SearchIcon from '@mui/icons-material/Search';
import ListIcon from '@mui/icons-material/List';
import InputAdornment from "@mui/material/InputAdornment";
import Tooltip from '@mui/material/Tooltip';



import Search from "./searchresults";
import History from "./history";
import Jot from "./jot";
import Find from "./find";
import X from './x.css'


if (typeof browser === "undefined") {
	var browser = chrome;
}


const JotFind = () => {

	const bg_dark = "#05508a";
	const bg_light = "#FAEBD7";


	const [page, set_page] = useState(true);
	const [txt, set_txt] = useState("");
	const [history, set_history] = useState(false);
	const [search_results, set_search_results] = useState(true);

	const [wid, set_wid] = useState(480);

	chrome.storage.local.get(['width'], function (e) {
		set_wid(e.width);
	});

	const data = { 'user_name': 'kat', 'block_text': 'this is the first jot. This is the second one', 'metadata': '{"source": "lol", "application": "pdf", "device": "iOS"}' }

	const handleHistory = () => {
		set_history(!history);
	}

	if (localStorage.getItem('theme') == undefined) {
		localStorage.setItem('theme', bg_light);
	}



	if (browser == undefined || browser == null) {

		var browser = chrome;
	}



	return (

		<div sx={{ width: wid }} >

			{page ? (
				<Jot />
			) : (
				<Find txt={txt} set_txt={set_txt}/>
			)}

			{search_results ? (
				<Grid item xs={12}>
					<Search txt={txt}/>
				</Grid>
			) : (
				<Grid item xs={12} />
			)}



			<Grid container spacing={2}>
				<Grid item xs={1} align="left">
					<Tooltip title="My Jots">
						<IconButton
							className="icon-button-blue"

							disableRipple
							onClick={handleHistory}>

							<ListIcon disabled />
						</IconButton>
					</Tooltip>
				</Grid>
				<Grid item xs={9}>

				</Grid>
				<Grid item xs={2} align="right">

					<IconButton
						className="icon-button"
						disableRipple
						sx={{ opacity: 0.2 }}
						onClick={() => { set_page(!page); }}>
						{page ? (
							<Tooltip title="Find">
								<SearchIcon disabled
								// sx={{opacity: 0.2}}

								/>
							</Tooltip>)
							: (
								<Tooltip title="Jot">
									<AddIcon disabled
									// sx={{opacity: 0.2}}
									/>
								</Tooltip>)}
					</IconButton>
				</Grid>

			</Grid>

			{
				history ? (
					<Grid item xs={12}>
						<History />
					</Grid>
				) : <Grid item xs={12}>
				</Grid>
			}





		</div >

	);
};

const IconTextField = ({ iconStart, iconEnd, InputProps, ...props }) => {
	return (
		<TextField
			{...props}
			InputProps={{
				...InputProps,
				startAdornment: iconStart ? (
					<InputAdornment position="start">{iconStart}</InputAdornment>
				) : null,
				endAdornment: iconEnd ? (
					<InputAdornment position="end">{iconEnd}</InputAdornment>
				) : null
			}}
		/>
	);
};

export default JotFind;

