/*global chrome*/
import * as React from "react";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { useState, useCallback } from "react";
import GoogleIcon from '@mui/icons-material/Google';
import TwitterIcon from '@mui/icons-material/Twitter';
import { IconButton } from "@mui/material";
import MailIcon from '@mui/icons-material/Mail';
import InputAdornment from "@mui/material/InputAdornment";


import { useAuth } from "../../contexts/AuthContext";

const theme = createTheme();

export default function SignIn() {
  const emailLogin = useAuth().emailLogin;
  const googleSignIn = useAuth().googleSignIn;
  const twitterSignIn = useAuth().twitterSignIn;

  const currentuser = useAuth().currentuser;
  // console.log(emailLogin)
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  const [email, setEmail] = useState("");

  const logindetails = {
    email: null,
    google: null,
    twitter: null
  }
  

  const emailSubmit = useCallback(
    async event => {
      // Added
      var actionCodeSettings = {
        url: "http://localhost:3050/",
        handleCodeInApp: true
      };


      event.preventDefault();
      const eml = document.getElementById("email").value;
      try {
        console.log(eml)
        await emailLogin(eml, actionCodeSettings);
        window.localStorage.setItem("emailForSignIn", eml);
      } catch (error) {
        alert(error);
      }
    },
    []
  );

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log({
      email: data.get("email"),
      password: data.get("password"),
    });
  };

  var browser = browser || chrome;

  const googleSubmit = () => {
    googleSignIn(); 
    //   url: "http://localhost:3050/googlesignin",
    // });
  };

  const twitterSubmit = () => {
    twitterSignIn();
  };

  const [wid, set_wid] = useState(480);

  chrome.storage.local.get(['width'], function (e) {
    set_wid(e.width);
  });

  

  return (
    <ThemeProvider>
      <Container component="main">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 2,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            width: wid
          }}
        >
          <Typography component="h1" variant="h5">
            Log In
          </Typography>
          <Box
            component="form"
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <IconTextField
                  margin="normal"
                  size="small"
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  autoFocus
                  iconEnd={
                    <IconButton
                      type="submit"
                      onClick={emailSubmit}>
                        
                      <MailIcon color="primary" />
                    </IconButton>
                  }

                />
              </Grid>
              {/* <Grid item xs={1}>
                <IconButton
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 2, mb: 1, ml: 1 }}
                  onClick={emailSubmit}
                  color="primary"
                >
                  <MailIcon />
                </IconButton>
              </Grid> */}
            </Grid>

            <Typography component="h4">OR</Typography>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 2, mb: 2 }}
              onClick={googleSubmit} 
              color="primary"
            >
              <GoogleIcon /> &nbsp; Google Sign In
            </Button>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 2, mb: 2 }}
              onClick={twitterSubmit}
              color="primary"
            >
              <TwitterIcon /> &nbsp; Twitter Sign In
            </Button>
            <Copyright sx={{ mt: 5 }} />
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'© '}
      KnoxEdge {','}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const IconTextField = ({ iconStart, iconEnd, InputProps, ...props }) => {
  return (
    <TextField
      {...props}
      InputProps={{
        ...InputProps,
        startAdornment: iconStart ? (
          <InputAdornment position="start">{iconStart}</InputAdornment>
        ) : null,
        endAdornment: iconEnd ? (
          <InputAdornment position="end">{iconEnd}</InputAdornment>
        ) : null
      }}
    />
  );
};