
/*global chrome*/

import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import CssBaseline from '@mui/material/CssBaseline';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useAuth } from "../../contexts/AuthContext";
import LogoutIcon from '@mui/icons-material/Logout';



import { Box, Grid, TextField, Button, MenuItem, Typography, IconButton, List, ListItem, ListItemIcon, ListItemText } from "@mui/material";
import { useState } from 'react';

import GoogleIcon from '@mui/icons-material/Google';
import TwitterIcon from '@mui/icons-material/Twitter';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import MailIcon from '@mui/icons-material/Mail';
import KeyboardIcon from '@mui/icons-material/Keyboard';
import InputAdornment from "@mui/material/InputAdornment";

export default function Settings() {
  // console.log(currentUser.providerData)
  const linkGoogle = useAuth().linkGoogle;
  const linkTwitter = useAuth().linkTwitter;
  const [wid, set_wid] = useState(480);

  chrome.storage.local.get(['width'], function (e) {
    set_wid(e.width);
  });

  const logout = useAuth()?.logout;
  const currentUser = useAuth()?.currentUser;
  const currentprovider = currentUser.providerData[0].providerId
  const provider_data = currentUser.providerData

  const [textdisabled, setDisabled] = React.useState(true);

  var usernamevalue = currentUser.displayName;
  var emailvalue = false;
  var googlevalue = false;
  var twittervalue = false;

  for (let i = 0; i < provider_data.length; i++) {
    if (provider_data[i].providerId === "google.com") {
      googlevalue = provider_data[i].displayName
    }
    else if (provider_data[i].providerId === "twitter.com") {
      twittervalue = provider_data[i].displayName
    }
    else {
      emailvalue = provider_data[i].email
    }
  }

  if (currentprovider === "google.com") {

    usernamevalue = currentUser.email;
    googlevalue = currentUser.displayName;
    // get email, twitter, username from backend
  }
  else if (currentprovider === "twitter.com") {

    usernamevalue = currentUser.displayName;
    twittervalue = currentUser.displayName;
    // get google, email, username from backend

  }
  else {

    usernamevalue = currentUser.email;
    emailvalue = currentUser.email;

    // get twitter, google, username from backend
  }


  const handleUsername = (e) => {
  }

  const handleEmailVerification = (e) => {
    console.log(document.getElementById("email").value)
  }

  const handleGoogleVerification = (e) => {
    linkGoogle();
  }

  const handleTwitterVerification = (e) => {
    linkTwitter();
  }

  return (
    <ThemeProvider>
      <Grid container component="main" sx={{ width: wid }}>
        <CssBaseline />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 2,
              mx: 2,
              display: 'flex',
              flexDirection: 'column',
              // alignItems: 'center',
            }}
          >
            <Box noValidate sx={{ mt: 1 }}>
              <Grid display="flex" justifyContent="right">
                <Button onClick={logout}>
                  <LogoutIcon color="primary" />
                  Logout
                </Button>
              </Grid>

              <Typography component="h1" variant="h5" align="left">
                Profile
              </Typography>
              <IconTextField
                size="small"
                margin="normal"
                required
                fullWidth
                id="username"
                disabled={textdisabled}
                name="username"
                placeholder={currentUser?.displayName}
                autoFocus
                iconStart={
                  <Button onClick={handleUsername}>Save</Button>
                }
                iconEnd={
                  <Button onClick={(e) => { setDisabled(!textdisabled) }}>Edit Handle</Button>
                }
              />

              <Typography component="h1" variant="h5" align="left">
                Authentication
              </Typography>

              {emailvalue ?
                (<TextField
                  margin="normal"
                  size="small"
                  required
                  fullWidth
                  name="email"
                  disabled
                  id="email"
                  placeholder={emailvalue}
                />) : (
                  <IconTextField
                    margin="normal"
                    size="small"
                    required
                    fullWidth
                    name="email"
                    id="email"
                    placeholder='Verify Email'
                    iconEnd={
                      <Button onClick={handleEmailVerification}>Verify Email</Button>
                    }
                  />)}

              <div>
                {googlevalue ?
                  (<Button sx={{ md: 2 }} color="error" fullWidth variant="contained"> <GoogleIcon /> &nbsp; {googlevalue}</Button>
                  ) : (
                    <Button sx={{ md: 2 }} onClick={handleGoogleVerification} fullWidth color="error" variant="contained"> <GoogleIcon /> &nbsp; Link with Google </Button>
                  )}

              </div>

              <div>
                {twittervalue ?
                  (<Button color="primary" fullWidth variant="contained"> <TwitterIcon /> &nbsp;  {twittervalue}</Button>
                  ) : (
                    <Button onClick={handleTwitterVerification} fullWidth color="primary" variant="contained"> <TwitterIcon /> &nbsp; Link with Twitter </Button>
                  )}
              </div>


            </Box>
          </Box>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
}

const IconTextField = ({ iconStart, iconEnd, InputProps, ...props }) => {
  return (
    <TextField
      {...props}
      InputProps={{
        ...InputProps,
        startAdornment: iconStart ? (
          <InputAdornment position="start">{iconStart}</InputAdornment>
        ) : null,
        endAdornment: iconEnd ? (
          <InputAdornment position="end">{iconEnd}</InputAdornment>
        ) : null
      }}
    />
  );
};