import React, { useContext, useState, useEffect } from "react";
import { auth } from "../firebase";
import { getAuth, signInWithPopup, GoogleAuthProvider, TwitterAuthProvider, signInWithRedirect, linkWithPopup } from "firebase/auth";
import { BrowserRouter } from "react-router-dom";

const provider_twitter = new TwitterAuthProvider();

const provider = new GoogleAuthProvider();
// const auth = getAuth();
const AuthContext = React.createContext();

export function useAuth() {
  return useContext(AuthContext);
}

export function AuthProvider({ children }) {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);

  function googleSignIn() {

    return signInWithPopup(auth, provider)
      .catch((error) => {
        // Handle Errors here.
        const errorCode = error.code;
        const errorMessage = error.message;
        // The email of the user's account used.
        const email = error.customData.email;
        // The AuthCredential type that was used.
        const credential = GoogleAuthProvider.credentialFromError(error);
        // ...
      });
  }

  function twitterSignIn() {
    return signInWithPopup(auth, provider_twitter)
      .then((result) => {
        console.log(TwitterAuthProvider.credentialFromResult(result));
        // This gives you a the Twitter OAuth 1.0 Access Token and Secret.
        // You can use these server side with your app's credentials to access the Twitter API.
        const credential = TwitterAuthProvider.credentialFromResult(result);
        const token = credential.accessToken;
        const secret = credential.secret;

        // The signed-in user info.
        const user = result.user;
        // ...
      }).catch((error) => {
        // Handle Errors here.
        console.log(error)
        const errorCode = error.code;
        const errorMessage = error.message;
        // The email of the user's account used.
        const email = error.customData.email;
        // The AuthCredential type that was used.
        const credential = TwitterAuthProvider.credentialFromError(error);
        // ...
      });
  }

  // function signup(email, password) {
  //   return auth.createUserWithEmailAndPassword(email, password);
  // }

  function emailLogin(email, actionCodeSettings) {
    return auth.sendSignInLinkToEmail(email, actionCodeSettings);
  }

  function isSignInLink(link) {
    return auth.isSignInWithEmailLink(link);
  }
  function signInLink(email, link) {
    return auth.signInWithEmailLink(email, link);
  }

  // function login(email, password) {
  //   return auth.signInWithEmailAndPassword(email, password);
  // }

  function logout() {
    return auth.signOut();
  }

  // function resetPassword(email) {
  //   return auth.sendPasswordResetEmail(email);
  // }

  function updateEmail(email) {
    return currentUser.updateEmail(email);
  }

  function linkGoogle() {
    return linkWithPopup(auth.currentUser, provider)
      .then((result) => {
        console.log("Accounts successfully linked.");
        const credential = GoogleAuthProvider.credentialFromResult(result);
        const user = result.user;
      })
      .catch((error) => {
        // Handle Errors here.
        const errorCode = error.code;
        const errorMessage = error.message;
        // The email of the user's account used.
        const email = error.customData.email;
        // The AuthCredential type that was used.
        const credential = GoogleAuthProvider.credentialFromError(error);
        // ...
      });
  }

  function linkTwitter() {
    return linkWithPopup(auth.currentUser, provider_twitter)
      .then((result) => {
        console.log(TwitterAuthProvider.credentialFromResult(result));
        // This gives you a the Twitter OAuth 1.0 Access Token and Secret.
        // You can use these server side with your app's credentials to access the Twitter API.
        const credential = TwitterAuthProvider.credentialFromResult(result);
        const token = credential.accessToken;
        const secret = credential.secret;

        // The signed-in user info.
        const user = result.user;
        // ...
      }).catch((error) => {
        // Handle Errors here.
        console.log(error)
        const errorCode = error.code;
        const errorMessage = error.message;
        // The email of the user's account used.
        const email = error.customData.email;
        // The AuthCredential type that was used.
        const credential = TwitterAuthProvider.credentialFromError(error);
        // ...
      });
  }

  // function updatePassword(password) {
  //   return currentUser.updatePassword(password);
  // }

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      setCurrentUser(user);
      setLoading(false);
    });

    return unsubscribe;
  }, []);

  const value = {
    currentUser,
    googleSignIn,
    twitterSignIn,
    // login,
    emailLogin,
    isSignInLink,
    signInLink,
    // signup,
    logout,
    // resetPassword,
    updateEmail,
    // updatePassword,
    linkGoogle,
    linkTwitter,
  };

  window.localStorage.setItem("userAuth", value);


  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
}