/*global chrome*/

import React, { useState, useEffect } from "react";
import { Grid, TextField, Button, MenuItem, Typography } from "@mui/material";
import { useNavigate } from "react-router-dom"
import axios from "axios"
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import ClearIcon from '@mui/icons-material/Clear';
import SearchIcon from '@mui/icons-material/Search';
import ListIcon from '@mui/icons-material/List';
import InputAdornment from "@mui/material/InputAdornment";
import Tooltip from '@mui/material/Tooltip';

import Search from "./searchresults";
import History from "./history";

if (typeof browser === "undefined") {
    var browser = chrome;
}

const Find = (props) => {

    const [cross, set_cross] = useState(false);
    const [search_results, set_search_results] = useState(false);

    const [wid, set_wid] = useState(480);

    chrome.storage.local.get(['width'], function (e) {
        set_wid(e.width);
    });


    return (
        <div sx={{ width: wid, padding: 1 }}>
            <IconTextField
                sx={{ width: wid, padding: 0, marginLeft: 1, marginRight: 1 }}
                className="inputRounded"
                iconStart={<IconButton
                    disableRipple
                    sx={{ pr: 0, pl: 0 }}
                    onClick={() => {
                        // make api call
                        set_search_results(true);
                    }}>
                    <Tooltip title="Find">

                        <SearchIcon
                            
                            color="primary" />
                    </Tooltip>
                </IconButton>}
                iconEnd={props.txt ?
                    <IconButton
                        disableRipple
                        sx={{ pr: 0, pl: 0 }}
                        onClick={(e) => { props.set_txt("") }}>
                        <ClearIcon
                            
                            color="primary" sx={{ opacity: 0.5 }} />
                    </IconButton>
                    :
                    null
                }
                hiddenLabel
                size="small"
                autoFocus
                variant="outlined"
                placeholder="Find related ..."
                value={props.txt}
                onChange={(e) => props.set_txt(e.target.value)}
            />
        </div >
    )
}

const IconTextField = ({ iconStart, iconEnd, InputProps, ...props }) => {
    return (
        <TextField
            {...props}
            InputProps={{
                ...InputProps,
                startAdornment: iconStart ? (
                    <InputAdornment position="start">{iconStart}</InputAdornment>
                ) : null,
                endAdornment: iconEnd ? (
                    <InputAdornment position="end">{iconEnd}</InputAdornment>
                ) : null
            }}
        />
    );
};

export default Find;

